import React from 'react';
import { usePathname } from 'next/navigation';



const Nav: React.FC = () => {
    const pathname = usePathname();

    return (
        <nav>
            <a className={pathname === "/" ? "current_route" : ""}>Início</a>
            <a className={pathname === "/infos" ? "current_route" : ""}>Informações Úteis</a>
            <a className={pathname === "/faq" ? "current_route" : ""}>FAQ</a>
            <a className={pathname === "/sobre" ? "current_route" : ""}>Sobre Nós</a>
            <a className={pathname === "/jogos" ? "current_route" : ""}>Jogos</a>
        </nav>
    )
}

export default Nav;